import { useEffect, useState } from 'react';
import type { MenuProps } from 'antd';
import { Menu } from 'antd';
import { useNavigate, useLocation } from 'react-router-dom';
import { getBreadcrumbList } from '@/utils/util';

// 定义 menu 类型
export type MenuItem = Required<MenuProps>["items"][number];

type SiderMenuProps = MenuProps & {
    menus: MenuItem[];
    isCollapse: boolean;
    setBreadcrumbList: (data: any) => void;
    onClick?: (e: any) => void;
    onOpenChange?: (v: string[]) => void;
};

const SiderMenu: React.FC<SiderMenuProps> = ({ menus, ...props }: SiderMenuProps) => {
    const navigateTo = useNavigate();
    const { isCollapse, setBreadcrumbList } = props;
    const { pathname, search } = useLocation();
    const [selectedKeys, setSelectedKeys] = useState<string[]>([pathname]);
    const [openKeys, setOpenKeys] = useState<string[]>([]);

    const getOpenKeys = (menus: MenuItem[], pathname: string) => {
        function findKey(obj: { key: string }) {
            return obj.key === pathname;
        }
        for (let i = 0; i < menus.length; i++) {
            if (menus[i]!['children'] && menus[i]!['children'].length > 1 && menus[i]!['children'].find(findKey)) {
                const firstOpenKey = menus[i]!.key as string;
                return firstOpenKey;
            }
        }
        return '';
    }

    useEffect(() => {
        const breadcrumbList = getBreadcrumbList(pathname, menus)
        setBreadcrumbList(breadcrumbList);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // 刷新页面菜单保持高亮
    useEffect(() => {
        setSelectedKeys([pathname]);
        if (!isCollapse) {
            setOpenKeys([getOpenKeys(menus, pathname)]);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pathname, isCollapse]);

    const menuClick = (e: any) => {
        if (props.onClick) {
            props.onClick(e);
        }
        const breadcrumbList = getBreadcrumbList(e.key, menus)
        setBreadcrumbList(breadcrumbList);
        navigateTo(`${e.key}${search}`);
    };

    const handleOpenChange = (keys: string[]) => {
        if (props.onOpenChange) {
            props.onOpenChange(keys);
        }
        setOpenKeys([keys[keys.length - 1]]);
    };

    return (<Menu selectedKeys={selectedKeys} mode="inline" items={menus} onClick={menuClick} onOpenChange={handleOpenChange} openKeys={openKeys} />);
};

export default SiderMenu;