import React, { useState } from 'react';
import { Layout } from 'antd';
import { Outlet } from 'react-router-dom';
import SiderMenu, { MenuItem } from '@/layout/components/SiderMenu';
import BreadCrumb from '@/components/Breadcrumb';
import { ApartmentOutlined, TeamOutlined, UserOutlined } from '@ant-design/icons';

const { Footer, Sider } = Layout;

const menuList: MenuItem[] = [{
    key: '/home/system/user',
    label: '用户管理',
    icon: <UserOutlined />
},
{
    key: '/home/system/role',
    label: '角色管理',
    icon: <ApartmentOutlined />
},
{
    key: '/home/system/team',
    label: '团队管理',
    icon: <TeamOutlined />
}];



const SystemLayou: React.FC = () => {
    const [collapsed, setCollapsed] = useState(false);
    let [breadcrumbList,setBreadcrumbList] = useState<string[]>([]);

    return (
        <Layout>
            <Sider collapsible theme="light" collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
                <SiderMenu menus={menuList} setBreadcrumbList={setBreadcrumbList} isCollapse={collapsed}></SiderMenu>
            </Sider>
            <Layout style={{overflowY:'auto',height:'100%'}}>
                <div style={{ margin: '8px 16px'}}>
                    <BreadCrumb data={breadcrumbList}></BreadCrumb>
                    <div style={{ minHeight: 360 }}>
                        <Outlet />
                    </div>
                </div>
                <Footer style={{ textAlign: 'center' }}>React + Ant Design</Footer>
            </Layout>
        </Layout>
    );
};

export default SystemLayou;